.App {
  font-family: inherit;
  text-align: center;
  width: 100%;
  background: inherit;
  margin: auto;
}
.wedding ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}

.wedding ul li {
  border: solid black;
  border-radius: 1em;
  margin: 1em;
  width: 30em;
  background-color: floralwhite;
}

.wedding ul li img {
  width: 90%;
  border: solid black;
  border-radius: 1em;
  margin: auto;
}
.wedding ul {
  list-style: none;
}
@media (max-width: 1024px) {
  .wedding .item {
    width: 90vw;
  }
}